import isString from './isString.js'
import isNumber from './isNumber.js'
import { divide } from './exactMath.js'

const defaultOptions = {
  fractionDigits: 2, // 
  padEnd: false, // 
  defaultValue: '', // , 
  onePercent: 0.01, // , 0.01 * 100 = 1, 1 * 100 = 100
  symbol: '%', // 
}

/**
 * 
 * %, 100%
 * @param value { string | number }
 * @param options { { fractionDigits: number, defaultValue: string, onePercent: number, padEnd: boolean, symbol: string } }
 */
export const percentage = (value, options = {}) => {
  options = Object.assign({}, defaultOptions, options)
  if (isString(value)) {
    if (options.symbol && value.includes(options.symbol)) {
      return value
    }
    value = Number.parseFloat(value)
  }

  if (!isNumber(value)) {
    return options.defaultValue
  }

  value = divide(value, options.onePercent)

  value = value.toFixed(options.fractionDigits)
  if (!options.padEnd) {
    value = Number.parseFloat(value)
  }

  return `${value}${options.symbol}`

}

export default percentage
